import { lazy, Suspense, createContext, useEffect, useState } from 'react';
import { BrowserRouter, Routes, Route, useNavigate, NavLink } from 'react-router-dom';
import { Container, Nav, Navbar } from 'react-bootstrap';
import './App.css'
import bg from './img/bg.jpg'
import data from './data.js'
import About from './pages/About.js'
import Event from './pages/Event.js'
import axios from 'axios'
import { useQuery } from 'react-query';


// import Detail from './pages/Detail.js'
// import Cart from './pages/Cart.js'
const Detail = lazy( () => import('./pages/Detail.js') );
const Cart = lazy( () => import('./pages/Cart.js') );


export let Context1 = createContext() //state 


function App() {

  //localStroge( , array/abject  json ) -  - Application
  //localStorage.setItem('age','20')    localStorage.getItem('age')     localStorage.removeItem('age')
  let obj = {name : 'kim'}
  localStorage.setItem('data', JSON.stringify(obj))
  let  = localStorage.getItem('data')
  console.log(JSON.parse().name);

  
  // -     localStorage   - wactched [0, 1]  :set
  //  Detail     id  localStorage Watched   <- 
  useEffect(()=>{
    localStorage.setItem('watched', JSON.stringify( [] ))
    
  },[])  


  let navigate = useNavigate();  //   
  let [shoes, setShoes] = useState(data);
  // let [] = useState([10, 11, 12]);
  let [click, setClick] = useState(0);
  let [hidden, setHidden] = useState(false);
  let [loading, setLoading] = useState(false);


  console.log(shoes[0].title);

  let moreProduct = function() {
    // https://sunshineyellow.tistory.com/134     

    setLoading(true); //   true    

    let url;
    if (click === 1) {
      url = 'https://codingapple1.github.io/shop/data2.json';
    } else if (click === 2) {
      url = 'https://codingapple1.github.io/shop/data3.json';
    } else if (click > 2) {
      setHidden(true);
      setLoading(false);
      // alert('');
      return;
    }
    // { 
    //   click === 1 && axios.get('https://codingapple1.github.io/shop/data2.json')
    //   click === 2 && axios.get('https://codingapple1.github.io/shop/data3.json')
    //   click === 3 && alert('');
    // }


    // ajax   3 1
    // 1. XMLHttpRequest  2. fetch()   3. axios
    axios.get(url)
    .then((res)=>{ 
      console.log(res.data);
      console.log(shoes);
      let newProduct = res.data; //   newProduct  
      let copy = [...shoes, ...newProduct]; //   (props.shoes)  (newProduct)    
      setShoes(copy); //      
      setLoading(false);
    })
    .catch(()=>{
      console.log('');
    })

    // Promise.all([ axios.get('/url1'), axios.get('/url2') ])
    // .then(()=>{
    // })
    // axios.post('/url1, {name : "kim"}')

    // fetch('https://codingapple1.github.io/shop/data2.json')
    // .then( => .json())
    // .then(data=>{})
  }

  let buttonClick = function() {
    setClick(click + 1); //      1 
  }

  useEffect(()=> {
    if (click !== 0) {
      moreProduct(); //   0  moreProduct     
    }
  }, [click]);


  
  let result = useQuery('', ()=>{
    return axios.get('https://codingapple1.github.io/userdata.json').then((a)=>{
      console.log('')
      return a.data
    }),
    { staleTime : 2000 } // 2  refetch()
  })
  
  return (
    <div className="App">

      <Navbar data-bs-theme="dark" className="navbar">
        <Container>
          <Navbar.Brand href="#">REACT SHOP</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link onClick={()=>{ navigate('/') }}></Nav.Link>
            <Nav.Link onClick={()=>{ navigate('/detail') }}></Nav.Link>
            <Nav.Link onClick={()=>{ navigate('/cart') }}></Nav.Link>
            <Nav.Link onClick={()=>{ navigate('/about') }} activestyle={{color : "green"}}></Nav.Link>
            <Nav.Link onClick={()=>{ navigate('/event') }} activeClassName="active"></Nav.Link>
          </Nav>
          <Nav className="me-auto">
            {/* { result.isLoading ? '' : result.data.name } */}
            { result.isLoading && '' }
            { result.error && '' }
            { result.data && result.data.name }
          </Nav>

        </Container>
      </Navbar>
      

      <Suspense fallback={ <div></div> }>
      <Routes>
        <Route exact path="/" element={
          <>
            <section className='main-bg' style={{ backgroundImage : 'url('+ bg +')' }}></section>
            <section className="content">
              <div className="row align-items-start">
                {
                  shoes.map((a, i)=> {
                    return (
                      <Card shoes={ shoes[i] } i={ i } key={ i }></Card>
                    )
                  })
                }
              </div>

              {hidden && <Hidden />} 
              {loading && <Loading />}
          
              {
                hidden == false
                ? <button className="btn btn-primary btn-lg" onClick={()=>{ buttonClick(); }}> </button>
                : null
              }              
            </section>

            {/*     */}
            <section className="recently">
              <h3>  </h3>
              <ul>
                {
                  shoes.map((a, i)=> {
                    return (
                      <li><img src='https://codingapple1.github.io/shop/shoes1.jpg'/></li>
                    )
                  })
                }
              </ul>
              <div>TOP</div>
            </section>


          </>
        } />

        <Route path="/detail/:id" element={ 
          // <Context1.Provider value={{ , shoes }}>
            <Detail shoes={shoes} />
          // </Context1.Provider>
        }></Route>
        
        <Route path="/cart" element={ <Cart/> } />

        {/* nested router  */}
        <Route path="/about" element={ <About/> }>
          <Route path="member" element={ <div></div> } />
          <Route path="location" element={ <div></div> } />
        </Route>

        <Route path="/event" element={ <Event/> }>
          <Route path="one" element={ <p>   </p> } />
          <Route path="two" element={ <p>  </p> } />
        </Route>

        <Route path="*" element={ <div>404page <br/>  </div> } />
      </Routes>
      
      </Suspense>

    </div>
  )
}

function Hidden() {
  return (
    <div className="alert alert-warning">
        .
    </div>
  )
}

function Loading() {
  return (
    <div className="alert alert-warning">
      ....
    </div>
  )
}

function Card(props) {
  return (
    <div className="col-md-4">
      <NavLink to="/detail/1"><img src={'https://codingapple1.github.io/shop/shoes'+ (props.i+1) +'.jpg'} width="80%" /></NavLink>
      <h4>{ props.shoes.title }</h4>
      <p>{ props.shoes.price }</p>
    </div>
  );
}


export default App;


//    & Redux 1 : Redux Toolkit 